.container-main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header {
  font-family: "Courier New", Courier, monospace;
  font-size: 40px;
  letter-spacing: 6px;
  word-spacing: 4.6px;
  font-weight: 700;
  text-decoration: none solid rgb(255, 255, 255);
  font-style: normal;
  font-variant: small-caps;
  text-transform: capitalize;
  padding: 60px;
  text-align: center;
}

.uploads {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-bottom: 50px;
}

.images {
  margin-top: 60px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.button {
  width: 100px;
  margin-top: 30px;
  margin-bottom: 50px;
  align-self: center;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
